  --cubic--: cubic-bezier(.4, 0, .2, 1);
}

@keyframes item-jump {
  0% {
    opacity: .5;
    transform: perspective(400px)translateY(-10px);
  }

  100% {
    opacity: 1;
    transform: perspective(400px)translateY(0);
  }
}

@keyframes item-rotate {
  0% {
    opacity: .3;
    transform: perspective(400px) rotate(0);
  }

  50% {
    opacity: .7;
    transform: perspective(400px) rotate(-90);
  }

  100% {
    opacity: 1;
    transform: perspective(400px) rotate(180);
  }
}

